import React from 'react';
import errorBoundary from '@src/components/errorBoundary';
import { useMaximize } from '@src/public/hooks/app';
import { groundsClose, isGrounds } from '@src/public/utils/app';
import styles from './index.css';

const Header = () => {
    const statusBarHeight = useMaximize();

    return (
        <div className={styles.wrap} style={{ paddingTop: statusBarHeight }}>
            {isGrounds ? (
                <div className={styles.content}>
                    <div
                        className={styles.back}
                        onClick={() => {
                            groundsClose();
                        }}
                    />
                    <div className={styles.title}>帮助与反馈</div>
                    <div className={styles.more} />
                </div>
            ) : null}
        </div>
    );
};

export default errorBoundary(Header);
