import React, { useState } from 'react';
import errorBoundary from '@src/components/errorBoundary';
import { Sheet } from '@src/mobile/components/ModalBox';
import styles from './index.css';
import Suggest from './Suggest';

const Bottom = () => {
    const [showSuggestModal, setShowSuggestModal] = useState(false);

    const closeSuggestModal = () => {
        setShowSuggestModal(false);
    };

    const content = (
        <>
            <div className={styles.bottomText}>期待您的反馈建议，帮助我们不断改进吧！</div>
            <div
                className={styles.bottomBtn}
                onClick={() => {
                    setShowSuggestModal(true);
                }}>
                <div className={styles.bottomBtnText}>发表建议</div>
            </div>
        </>
    );

    return (
        <>
            <div className={styles.placeholder}>{content}</div>
            <div className={styles.wrap}>{content}</div>
            <Sheet show={showSuggestModal} dragToClose={false} onClose={closeSuggestModal}>
                <Suggest />
            </Sheet>
        </>
    );
};

export default errorBoundary(Bottom);
